.nav-button {
  display: flex;
  position: fixed;
  right: 1em;
  top: 1em;
  width: 4em;
  height: 4em;
  border: 0;
  border-radius: 20%;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 0px 12px 8px black;
  z-index: 3;
  transition: all 0.1s ease-out;
}
.nav-button:active {
  background-color: gray;
}

.nav-button-expanded {
  transform: rotate(90deg);
  color: aliceblue;
  transition: all 0.1s ease-out;
}

.nav-button-icon {
  width: 3em;
  height: auto;
  color: black;
}

.mobile-nav-background {
  background-color: #111d;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 1;
}
.mobile-nav-list {
  display: flex;
  height: 60vh;
  margin-top: 12vh;
  margin-bottom: 5vh;
  margin-right: 10vw;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
}
.mobile-nav-link {
  font-size: 12em;
  text-decoration: none;
}
.mobile-nav-link:visited {
  color: aliceblue;
}

.mobile-navbar {
  position: absolute;
  left: 0;
  top: 0;
}
.mobile-nav-header {
  display: inline-flex;
  flex-direction: row;
  width: fit-content;
  color: darkgray;

  font-size: 2.25em;
  text-decoration: none;
  margin-top: 0.5em;
  margin-right: auto;
  margin-left: 1em;
  white-space: nowrap;
}
.mobile-nav-header:hover {
  color: azure;
}
h1.mobile-nav-header {
  margin: auto;
}
.mobile-nav-logo {
  width: 5em;
  height: auto;
  margin-right: 2vw;
}
