.widget {
  flex-direction: column;
  background-color: #282e39;
  padding: calc(6px + 0.5vw) calc(8px + 0.5vw) calc(10px + 1vw) calc(8px + 0.5vw);
  position: relative;
  margin: 0 auto 6em 0;
  background-image: url(https://store.akamai.steamstatic.com/public/images/v6/steam_ico.png);
  background-repeat: no-repeat;
  background-position-x: 99%;
  background-position-y: 5%;
}

.widget-header {
  display: flex;
  flex-direction: row;
  font-size: calc(18px + 1vw);
  font-weight: normal;
}
h5.head {
  color: aliceblue;
  margin: calc(12px + 0.25vw) 0 calc(6px + 0.5vw) 0;
}
h5.tail {
  color: #999;
  margin: calc(12px + 0.25vw) 0 calc(6px + 0.5vw) calc(4px + 0.25vw);
}

.desc {
  font-size: calc(8px + 0.8vw);
  /* font-size: min(2em, 20px); */
  color: #c9c9c9;
  line-height: calc(16px + 1vw);
  /* line-height: min(1.5em, 24px) */
}
.logo {
  float: left;
  margin: 2px 10px calc(4px + 0.25vw) 0;
  width: calc(100px + 8vw);
  height: auto;
}

.wishlist-container {
  display: flex;
  position: absolute;
  right: 1em;
  font-size: 1.25em;

  color: #b8dc89;
  border: 1px solid #232a0e;
  border-radius: 0.2em;
  background: -webkit-linear-gradient(30deg, #395021 5%, #283e18 95%);
  background: linear-gradient(60deg, #395021 5%, #283e18 95%);
}
.date {
  float: left;
  font-size: 1.25em;
  margin: auto 0.2em;
}
.wishlist-button {
  margin-inline: 0.5em;
}
.wishlist-button-content {
  font-size: 1.5em;
  font-weight: auto;
  color: #d2efa9;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
  text-decoration: none;
  padding: 0.25em 0.5em;
  line-height: 2.5em;
  border-radius: 2px;
  background: #75b022;
  cursor: pointer;
}
.wishlist-button-content:hover {
  color: aliceblue;
  background: #a7e84b;
}
.wishlist-button-content:visited {
  color: #d2efa9;
}
